<template>
    <div>
        <!--我的订单列表-->
        <el-table
            ref="price"
            highlight-current-row
            :data="myorderData"
            :height="tabHeight"
            class="adjust-table table-line-hight">
            <el-table-column label="编号(状态)">
                <template slot-scope="scope">
                    <p class="main-text">{{scope.row.ordernum}}</p>
                    <p class="secondary-text">{{scope.row.statusname}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="buildsite" label="工程项目"></el-table-column>
            <el-table-column prop="goodsname" label="产品名称"></el-table-column>
            <el-table-column prop="qtyorder" label="下单放量"></el-table-column>
            <el-table-column prop="buildpartname" label="浇筑部位" width="100"></el-table-column>
            <el-table-column label="浇筑方式(塌落度)">
                <template slot-scope="scope">
                    <p class="main-text">{{scope.row.buildwayname}}</p>
                    <p class="secondary-text">{{scope.row.buildslumpname}}</p>
                </template>
            </el-table-column>
            <el-table-column label="送货时间(联系人/联系电话)">
                <template slot-scope="scope">
                    <p class="main-text">{{scope.row.builddate}}</p>
                    <p class="secondary-text">{{scope.row.contact}}({{scope.row.phone}})</p>
                </template>
            </el-table-column>
            <el-table-column label="过磅(带资料)">
                <template slot-scope="scope">
                    <p class="main-text">{{scope.row.isweight}}</p>
                    <p class="secondary-text">{{scope.row.istakefile}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="remark" label="备注"></el-table-column>
            <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                    <el-button type="text" @click="show_cOrderCheck(scope.row)">复制</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
    
</template>

<script>
    // import ZLCommonTable from '../../../../components/ZLCommonTable'
    export default {
        // components:{
        //     ZLCommonTable,
        // },
        data() {
            return {
                tabHeight: 240,
                navs:this.$store.getters.getSideNavlist||[],
                sideValue: sessionStorage.getItem('sideValue')!==undefined?parseInt(sessionStorage.getItem('sideValue')):0,
                sysNav: [],
                myorderData:[],
                searchForm:{
                    exts:{
                        builddate_ge_date:this.recentDate(0),
                        builddate_le_date:this.recentDate(1),
                        //ordernum_like:'SN0016128'
                    }
                },
            }
        },

        activated() {
            this.getlist();
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                //this.initEchart();
               // this.setEchart();
                this.sysNav = this.navs[this.sideValue].children;
                this.getlist();
            },

            //获取数据
            getlist(){
                let tempData = [];
                this.fetch('/mktmfc/clientOrder/findAll?pageNo=1' + '&pageSize=15' + '&orderBys=createtime_desc',this.searchForm)
                    .then(result => {
                        if(result.status === 200){
                            tempData = result.data.list;
                            for(let i=0; i<tempData.length; i++){
                               tempData[i].statusname = this.returnStatus(tempData[i].status)
                               if(tempData[i].isweight === 0){
                                    tempData[i].isweight = '否'
                               }else{
                                    tempData[i].isweight = '是'
                               }

                               if(tempData[i].istakefile === 0){
                                    tempData[i].istakefile = '否'
                               }else{
                                    tempData[i].istakefile = '是'
                               }
                            }
                            this.myorderData = tempData
                        }else {
                            this.$message({
                                message:this.returnIntercept(result),
                                type:'error'
                            });
                           
                        }
                    })
            },

            //复制
            show_cOrderCheck(row){
                row.istakefile === '否'?row.istakefile=false:row.istakefile=true;
                row.isweight === '否'?row.isweight=false:row.isweight=true;
                this.$store.state.copyForm = row;
                this.$store.state.copy = 1;
                this.goTargetTag(this.filterArray(this.sysNav,'resname','自助下单'));
            }
        }
    }
</script>

<style lang="less" scoped type="text/less">
    .notice-div{
        margin-top: 10px;
        margin-left: 20px;
        display: flex;
        justify-content: space-between;
        .spanII{
            margin-right: 10px;
        }
    }
</style>


